<template>
	<div id="nav">
		<!-- 网站导航 -->
		<header v-bind:style="heightnav">
			<nav ref="nav">
				<a href="#">
					<img :src="nav['headereor']" v-real-img="nav['head']">
					<h2>{{nav['title']}}</h2>
					<i class="iconfont icon-xiala nav-pull" title="下拉更多" @click="navPull"></i>
				</a>
				
				<router-link :to="item.link" v-for="item in nav.lable" :key="item.nav" :title="item.title" >
					<i class="iconfont" :class="item.icon">
						
					</i>
					<span>
						{{item.title}}
					</span>
				</router-link>
			</nav>
		</header>
	</div>
</template>

<script>
	export default {
	  name: "Nav",
	  data() {
	  	return {
			heightnav:{
				height:""
			},
			ison:true,
	  		nav: {
				title: " Cloud",
				head:require("../../../public/img/20208021.jpg"),
				headereor:require("../../../public/img/error.png"),
				
				lable:[
					{
						title:"主页",
						icon:"icon-home",
						link:"/",
					},
					{
						title:"音乐",
						icon:"icon-classify",
						link:"/music",
					},
					{
						title:"关于我",
						icon:"icon-guanyuwomen",
						link:"/about",
					}
				],
				
			},
			
	  	}
	  },
	  methods:{
		navPull: function(){
			if(this.$refs.nav.clientHeight === 65){
				this.$refs.nav.style.height = (this.$refs.nav.children.length-1) * (this.$refs.nav.children[1].clientHeight+20) + this.$refs.nav.children[0].clientHeight+20+"px";
			}else{
				this.$refs.nav.style.height = "65px"
			}
		},
		resizeFun: function(){
			let timer;
			let $this = this;
			clearTimeout(timer);
			timer = setTimeout(function(){
				if(document.documentElement.clientWidth >= 580){
					$this.$refs.nav.style.height = "auto"
				}
				if(document.documentElement.clientWidth < 560){
					$this.$refs.nav.style.height = $this.$refs.nav.clientHeight+"px";
				}
			},200)
		}
	  },
	  mounted() {
		this.resizeFun();
		window.addEventListener("resize",this.resizeFun);
	  },
	};
</script>

<style lang="less" scoped>
	// @import url("//at.alicdn.com/t/font_1977742_2u96qmxg0st.css");
	// @主题切换样式提取***********************************
	@roterlinkColor:rgba(255,255,255,.15);
	@navbgColor:#1B1C1D;
	@fontColor:#000;
	@borderShadow:0 0 14px 0px rgba(195, 122, 17, .91);
	@iColor:#fff;
	@spanColor:rgba(255,255,255,.7);
	@hColor:#fff;
	//****************************************************
	#nav{
		box-shadow: @borderShadow;
		.router-link-exact-active{
			background-color: @roterlinkColor;
			border-radius: 5px;
			transition: all .2s;
		}
		header{
			transition: all .3s;
			nav{
				display: flex;
				align-items: center;
				justify-content: center;
				flex-wrap: wrap;
				background-color: @navbgColor;
				transition: all .5s;
				
				.nav-pull{
					margin-left: 20px;
					cursor: pointer;
					padding: 10px;
					display: none;
					margin-left: auto;
				}
				h2{
					color: @hColor;
					margin-left: 5px;
				}
				span{
					color: @spanColor;
					margin: 0 5px;
				}
				a{
					display: flex;
					justify-content: center;
					align-items: center;
					padding: 10px 20px 10px 20px;
					color: #fff;
					margin: 10px 5px 10px 5px;
					
					img{
						border-radius: 50%;
						width: 45px;height: 45px;
						box-shadow: 0 0 5px 1px #33b599;
					}
				}
				a:first-child{
					padding: 0;
				}
				a:not(:first-child):hover,.nav-pull:hover{
					background-color: rgba(255,255,255,.08);
				}
				i{
					color: @iColor;
					font-size: 20px;
					
				}
			}
			
			@media screen and (max-width: 860px) {
				// 媒体查询
				nav{
					h2,span{
						display: none;
					};
				}
			}
			@media screen and (max-width: 580px) {
				// 媒体查询
				header{
					
				}
				nav{
					 justify-content: flex-start;
					 
					// padding: 0 10px;
					a{
						display: flex;
						width: 100%;
						justify-content: flex-start;
					}
					span{
						display: block;
					}
					h2{
						display: block;
					}
					.nav-pull{
						display: block;
					}
				}
			}
		}
	}
</style>
