import Vue from "vue";
import Vuex from "vuex";
import aritlcejson from "../../public/data/aritlce.json"; 
Vue.use(Vuex);

export default new Vuex.Store({
  state: {
	aritlce:aritlcejson.data,
  },
  getters:{
	  aritlceLen: (state)=>{
		  return state.aritlce.length;
	  },
	  aritlceTgasLen: (state)=>{
		  let arr = []
		  state.aritlce.map((item)=>{
			arr.push(...item.tgas);
		  });
		  let res = arr.filter((item, index)=>{
			  return arr.indexOf(item) === index
		  })
		  return res.length
	  },
	   aritlceTgas: state=>{
		   let tgas = new Array();
		   state.aritlce.map((item)=>{
				tgas.push(item.tgas);
		   });
		  let a =  tgas.flatMap((item)=>{
			   return item
		   })
		   return a
	   },
	   
  },
  mutations: {
	
  },
  actions: {},
  modules: {}
});
