<template>
  <div id="app">
    <Nav></Nav>
    <div class="flex-1">
      <!-- flex-1 默认占满屏幕 -->
      <router-view></router-view>
    </div>
    <Tail></Tail>
	<loading v-if="loadingView"></loading>
  </div>
</template>
<script>
import Nav from "./components/nav/Nav.vue";
import Tail from "./components/Tail.vue";
import Loading from "./components/loading/Loading.vue"
export default {
  name: "App",
  components: {
    Nav,
    Tail,
	  Loading
  },
  mounted() {
  	window.onload = ()=>{
		setTimeout(()=>{
			this.loadingView = false
		},1000);
	}
  },
  data() {
  	return {
  		loadingView: true
  	}
  },
};
</script>
<style lang="less">

#app {
  position: relative;
  min-height: 100vh;
  display: flex;
  flex-direction: column;

  .flex-1 {
    flex: 1;
	z-index: 1;
  }
}
</style>
